div {
  font-family: "Josefin Sans", sans-serif;
}

body {
  background-image: url(https://wallpaperaccess.com/full/130061.png);
}

h1 {
  font-family: "Playfair Display", serif;
}

h4 {
  padding: 20px;
}

/* Header */
.header img:hover {
  opacity: 0.9;
  text-decoration: none;
}

.header:hover {
  text-decoration: none;
  color: white;
}

.header {
  color: white;
  background-color: black;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  width: 100%;
}

.header-div {
  padding-bottom: 30px;
}

.nav-link {
  color: white;
  font-family: "Josefin Sans", sans-serif;
  margin-top: 8px;
  margin-left: 2px;
}

.nav-link:hover {
  color: #ff1616;
}

.logo {
  width: 130px;
  padding-right: 20px;
  padding-left: 30px;
}

/* Home */

.home {
  text-align: center;
}

.main-image {
  padding: 20px;
}

.marble-div {
  margin: 40px;
}

div .container-fluid {
  padding: 0px;
}

.home-text {
  background-color: #ffd330;
  font-size: 17px;
  color: black;
  padding-top: 30px;
  padding-bottom: 50px;
}

.home-text .link {
  color: black;
  text-decoration: none;
  transition: 0.4s;
}

.home-text .link:hover {
  color: white;
}

.home .wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-template-rows: 1fr;
  grid-gap: 20px;

  margin: auto;
  position: relative;
}

.home .icon {
  width: 80px;
  margin-top: 20px;
  margin-bottom: 10px;
}

.home .icon-each {
  transition: 0.2s;
}

.icon-text {
  font-size: 2rem;
  color: white;
}

.icon-text {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  padding-bottom: 50px;
}

.home .info {
  padding-top: 80px;
  padding-bottom: 50px;
}

/* RecipeList */

.recipe-list .container {
  padding-top: 30px;
  padding-bottom: 20px;
}

.link .recipe {
  color: black;
  text-decoration: none;
  transition: 0.4s;
}

.link .recipe:hover {
  color: white;
}

.link:hover {
  text-decoration: none;
  color: white;
}

.recipe-list .list-group-item h3 {
  text-align: center;
}

.recipe-list .wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-rows: 1fr 1fr;
  grid-gap: 10px;
  margin: auto;
  position: relative;
  justify-items: center;
}

.recipe-list .grid-one {
  grid-column: 1;
  grid-row: 1;
}

.recipe-list .grid-two {
  grid-column: 2;
  grid-row: 1;
}

.recipe-list .info {
  padding: 60px;
  font-size: 20px;
}

.recipe-list .img-fluid {
  max-width: 30rem;
  border-style: solid;
  border-color: #ffd330;
  padding: 20px;
  margin: 20px;
}

.recipe-card .link {
  color: black;
}

.card {
  height: 100%;
}

.card-body {
  height: 120px;
}

/* Footer */

.footer {
  background-color: black;
  text-align: center;
  color: white;
}

.footer-content {
  padding-top: 30px;
  padding-bottom: 15px;
}

/* Recipe Create */

.page-title {
  color: black;
  background-color: #ffd330;
  font-family: "Josefin Sans", sans-serif;
  font-size: 50px;
  padding: 80px 0px 20px 0px;
  text-align: center;
}

.create .container {
  margin-top: 20px;
}

.create-form-background {
  background-color: rgba(255, 255, 255, 0.5);
  padding: 10px;
}

.create-form {
  width: 100%;
}

.photo-form {
  margin-left: 30px;
}

.file-input {
  width: 100%;
}

.submit-button {
  margin-top: 40px;
}

.render-field {
  padding: 20px;
}

/* Show Recipe */

.recipe-show {
  text-align: center;
  margin: auto;
}

.recipe-show-box {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 30px 0px;
}

.recipe-show h4 {
  border-bottom-style: solid;
  border-bottom-width: 1px;
  background-color: black;
  color: white;
}

.recipe-image {
  margin: auto;
  width: 100%;
  height: auto;
}

.recipe-show .wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-template-rows: 2fr;
  margin: auto;
  position: relative;
  justify-items: center;
  padding: 10px 10px 50px 10px;
}

.text-column {
  text-align: left;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);
}

.ingredients-and-steps {
  padding: 20px 0px 20px 0px;
  margin-left: 30px;
}

.recipe-show-item {
  width: 100%;
}

/* Recipe Upload */

.existing-image .image-button {
  position: absolute;
  top: 6%;
  left: 68%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);

  width: auto;
}
.existing-image {
  text-align: center;
  position: relative;
}

.the-image {
  width: 100%;
  max-width: 600px;
}

@media screen and (max-width: 1100px) {
  .existing-image .image-button {
    left: 75%;
  }
}

@media screen and (max-width: 600px) {
  .recipe-image {
    width: 100%;
    height: auto;
  }

  .existing-image .image-button {
    font-size: 10px;
    top: 10%;
    left: 78%;
  }

  .header-div {
    padding-bottom: 70px;
  }

  .page-title {
    padding: 30px 0px 20px 0px;
    font-size: 2rem;
  }
}

.filler {
  height: 800px;
}

.stack-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.stack-item1 {
  grid-column-start: 1;
  grid-column-end: 5;
}

.stack-item2 {
  grid-column-start: 5;
  grid-column-end: 5;
}

/* Please sign in */

.sign-in-page {
  text-align: center;
  padding: 200px 0px;
}

.sign-in-prompt {
  color: white;
  background-color: black;
  padding: 20px;
  border-color: #ffd330;
  border-style: solid;
  border-width: 10px;
  width: 100%;
}

.sign-in-prompt img {
  padding: 20px;
  padding-top: 40px;
}
